.title {
	@include verdana;
	@include f(44px, 700, 1.23);
	color: $black;
	letter-spacing: normal;
	@include md {
		font-size: 25px;
		line-height: 1.33; }
	&_xl {
		font-size: 36px;
		line-height: 1.5;
		@include sm {
			font-size: 25px;
			line-height: 1.24; } }
	&_lg {
		font-size: 30px;
		line-height: 1.34;
		@include md {
			font-size: 20px; } }
	&_md {
		font-size: 21px;
		line-height: 1.48;
		@include md {
			font-size: 19px; } }
	&_sm {
		font-size: 18px;
		line-height: 1.72;
		@include md {
			font-size: 15px; } }
	&_white {
		color: $white; }
	&_line {
		span {
			display: inline-block;
			position: relative;
			padding-right: 2.25em;
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				// margin-top: -3px
				right: 0;
				width: 1.86em; // 82px
				height: 6px;
				background-color: currentColor; } } } }

.title-m {
	@include verdana;
	@include f(30px, 700, 1.8);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 25px;
		line-height: 1.24; } }
.title-lg {
	@include verdana;
	@include f(44px, 700, 1.22);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 30px;
		line-height: 1.33; } }


.article,
article {
	&__title,
	h1 {
		&:not(:last-child) {
			margin-bottom: 30px; } }
	h1 {
		@extend .title; }
	p {
		margin-top: 30px;
		max-width: 900px;
		@include ms {
			line-height: 1.8;
			padding-top: 20px; } } }
